.box {
    width: 100%;
    margin-bottom: 10px;
}

.box input,
.box textarea {
    margin-bottom: 0 !important;
}

.box.error input,
.box.error textarea {
    border: 2px solid red;
}

.error {
    margin-top: 5px;
    display: block;
    color: red;
    font-size: 14px;
    font-weight: 500;
}